import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import styled from "styled-components";
import { navigation } from "../config/mock-response";

const StyledImage = styled.img`
  width: 30px;
  height: 25px;
`;

const NavWrapper = styled.div`
  padding-top: 0.75em;
  display: flex;
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  & .nav-link {
    color: #679b9b !important;
    margin: 0.5%;
    text-decoration: none;
    padding: 4px;
  }
  & .nav-link:hover {
    border: solid #679b9b 1px;
    margin: 0.5%;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    text-decoration: none;
    color: #679b9b;
    padding: 3px;
  }
`;

const FlagWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-top: 1rem;
  padding: 0.5rem;
`;

const Navigation = () => {
  const [message, setMessage] = useState(navigation);

  const options = [
    {
      value: "italy",
      label: (
        <div>
          <StyledImage src="image/italianflag.png" alt="italiano" />
        </div>
      ),
    },
    {
      value: "english",
      label: (
        <div>
          <StyledImage src="image/englishflag.png" alt="english" />
        </div>
      ),
    },
  ];

  const customStyles = {
    option: (provided, state) => ({
      ...provided,
      padding: 0,
      backgroundColor: "#fde2e2",
    }),
    control: (styles) => ({
      ...styles,
      backgroundColor: "transparent",
      width: 80,
      border: 0,
      boxShadow: "none",
    }),
    singleValue: (provided, state) => {
      const opacity = state.isDisabled ? 0.5 : 1;
      const transition = "opacity 300ms";

      return { ...provided, opacity, transition };
    },
  };

  return (
    <div>
      <NavWrapper>
        <NavLink className="nav-link" to="/hang-hai-wedding-invitation">
          {message?.home}
        </NavLink>
        {/* <NavLink className="nav-link" to="/wedding">
          {message?.wedding}
        </NavLink> */}
        <NavLink className="nav-link" to="/gallery">
          {message?.gallery}
        </NavLink>
      </NavWrapper>
    </div>
  );
};

export default React.memo(Navigation);
